
import { showAlert, validateIfFormFilled } from './function.js';
import { newCustomer } from './API.js';

( () => {
    const form = document.querySelector( '#formulario' );

    form.addEventListener( 'submit', validateCustomer );

    function validateCustomer( e ) {
        e.preventDefault();

        const name = document.querySelector( '#nombre' ).value;
        const email = document.querySelector( '#email' ).value;
        const tel = document.querySelector( '#telefono' ).value;
        const company = document.querySelector( '#empresa' ).value;

        const customer = {
            name,
            email,
            tel,
            company,
        }

        if ( !validateIfFormFilled( customer ) ) {
            showAlert( 'All inputs are mandatory.' );
            return;
        }

        newCustomer( customer );

    };


} )();